:root {
  --primary-color: #006FB2;
  --secondary-color: #0D0D0D;
  --large: 2em;
  --medium: 1.25em;
  /* primary colors */
  --black: #000000; 
  --lt-gray: #DDDDDD;
  --md-gray: #676767;
  --dk-gray: #444444;
  --cod-gray: #0D0D0D;
  /* secondary colors -  */
  --lt-mine-shaft: #333333;
  --dk-mine-shaft: #222222;
  --white: #FFFFFF;
  /* fonts */
  --primary-font: "Visby DemiBold";
  --line-spacing: 1.5em;
  /* logo clear space: 10% width */
  /* logo on bg img: add black layer with 60% transparency */

}

* {
  box-sizing: border-box;
}

body {
  background: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: 'Rajdhani', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background: var(--white);
  margin: 0 auto;
  max-width: 1024px;
}
.contact-bar {
  background: var(--primary-color);
  text-align: right;
  padding: 0.5em;
}

.link {
  color: var(--lt-gray);
  display: inline-block;
  text-decoration: none;
  margin-right: 3em;
  font-weight: bold;
  -webkit-transition: color 300ms;
  transition: color 300ms;
}

.link:hover {
  color: var(--white);
}
.nav-bar {
  align-items: center;
  background: var(--secondary-color);
  color: white;
  display: flex;
  justify-content: space-around;
  height: 6em;
  padding: 0.5em;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 200ms;
  transition: all 200ms;
  z-index: 1;
}
.small-nav-bar {
  height: 4em;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}
.nav-image {
  height: 100%;
}
.nav-link {
  color: var(--lt-gray);
  font-size: var(--medium);
  text-decoration: none;
  -webkit-transition: color 300ms;
  transition: color 300ms;
  padding: 1em;
}
.nav-link:hover, .nav-link-active {
  color: #f8eda1;
}
@media (min-width: 768px) {
  .nav-link {
    font-size: var(--large);
  }
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: var(--secondary-color);
  z-index: 2;
}
.button {
  background: var(--primary-color);
  color: var(--white);
  cursor: pointer;
  display: inline-block;
  text-transform: uppercase;
  margin: 0 0.25em 0.25em;
  padding: 0.5em 1em;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.button:hover {
  opacity: 0.8;
}
.splash-wrapper {
  position: relative;
}
.splash-container {
  position: relative;
  background-repeat: no-repeat;
  background-size: auto 300px;
  overflow: hidden;
  padding: 2em 4em 0;
  text-align: center;
}
.splash-content-wrapper {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding-left: 10%;
}
.splash-title, .splash-subtitle {
  display: inline-block;
  padding: 0.1rem 0.5rem;
}
.splash-title {
  font-size: var(--large);
  margin-bottom: 0.5em;
}
.splash-subtitle {
  font-size: var(--medium);
  margin-bottom: 1em;
}
.splash-button {
  background: var(--primary-color);
  color: var(--white);
  display: inline-block;
  font-size: 1.5em;
  margin: 0 0.5em 0 0;
  padding: 0.5em 1em;
  text-decoration: none;
}
.separator {
  margin-top: 2em;
}

@media (min-width: 768px) {
  .splash-container {
    background-size: cover;
  }
  .splash-wrapper {
    margin-bottom: 0 !important;
    overflow: hidden !important;
    height: auto !important;
  }
  .splash-container {
    text-align: left;
  }
  .splash-content {
    margin-top: 0 !important;
  }
  .splash-title {
    margin-top: 0.5em !important;
    font-size: 3em;
  }
  .splash-subtitle {
    font-size: 2em;
  }
  .splash-title, .splash-subtitle {
    background: rgba(0, 0, 0, 0.5);
    color: var(--white);
    text-align: left;
  }
}
@media (max-width: 768px) {
  .splash-content {
    opacity: 1 !important;
  }
}
.register {
  padding: 1em;
  text-align: center;
}
.info {
  display: flex;
  width: 100%;
  padding-top: 1em;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
}
.info-title {
  margin-bottom: 0.5em;
}
.info-text-container {
  flex-grow: 1;
  flex-basis: 300px;
  line-height: 1.75em;
  padding: 0 2em 1em;
}
.info-text {
  line-height: 1.75em;
}
.info-video-container{
  background: #000;
  flex-basis: 400px;
  flex-shrink: 0;
  margin: 0 3em 2em;
}
.reverse {
  flex-direction: row-reverse;
}

@media (min-width: 768px) {
  .info-video-container{
    margin: 2em 3em;
  }
}
.hero {
  padding: 2em;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-content{
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  color: white;
  z-index: 1;
}
.hero-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.separator {
  margin: 2em 2em 1em;
}
@media (min-width: 768px) {
  .separator {
    display: none;
  }
}
.contact-form {
  padding: 3em 2em 0;
}
.contact-form-title {
  margin: 0 0 -0.1em;
  padding: 0;
  text-align: center;
  font-weight: normal;
  font-size: var(--large);
}
.contact-form-subtitle {
  padding: 0;
  display: flex;
  align-items: center;
  font-size: 1rem;
}
.contact-form-subtitle>h3 {
  margin: 0;
  padding: 0 1em;
}
.contact-form-subtitle>hr {
  flex-grow: 1;
  border-color: red;
}
.contact-form-label {
  padding: 0.25em;
}
.contact-form-input {
  width: 100%;
  padding: 1em;
  font-family: 'Rajdhani', sans-serif;
  font-size: 1rem;
  color: black;
  border: 1px #DDD solid;
}
.contact-form-input:focus {
  outline: none;
  border: 1px #444 solid;
}
.sell-image {
  width: 100%;
}
.sell-top {
  margin-top: 2em;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.sell-top>div:first-child {
  flex-basis: 400px;
  flex-grow: 1;
}
.sell-top>div:last-child {
  flex-basis: 300px;
  flex-grow: 1;
}
.card {
  padding: 1em;
}
.card-video-container {
  width: 100%;
  margin-bottom: 1em;
}
.card-text-container {
  line-height: 1.75;
}
.two-column, .three-column {
  display: flex;
  flex-wrap: wrap;
}
.two-column>div{
  flex-basis: 400px;
  flex-grow: 1;
}

.three-column {
  padding: 1em;
}

.three-column>div {
  flex-basis: 300px;
  flex-grow: 1;
}

.buy-text {
  padding: 0 2em;
}
.newsletter-form {
  padding: 3em 2em 0;
}
.newsletter-form-title {
  margin: 0 0 -0.1em;
  padding: 0;
  text-align: center;
  font-weight: normal;
  font-size: var(--large);
}
.newsletter-form-subtitle {
  padding: 0;
  display: flex;
  align-items: center;
  font-size: 1rem;
}
.newsletter-form-subtitle>h3 {
  margin: 0;
  padding: 0 1em;
}
.newsletter-form-subtitle>hr {
  flex-grow: 1;
  border-color: red;
}
.newsletter-form-label {
  padding: 0.25em;
}
.newsletter-form-input {
  width: 100%;
  padding: 1em;
  font-family: 'Rajdhani', sans-serif;
  font-size: 1rem;
  color: black;
  border: 1px #DDD solid;
}
.newsletter-form-input:focus {
  outline: none;
  border: 1px #444 solid;
}
.contact-container {
  display: flex;
  flex-wrap: wrap;
  line-height: 1.75em;
}
.contact-text, .contact-information {
  padding: 2em;
}
.contact-container>div {
  flex-basis: 300px;
  flex-grow: 1;
  padding-right: 2em;
}
.footer {
  padding: 2em;
  background: var(--black);
  color: var(--lt-gray);
  flex-wrap: wrap;
}
.footer>div {
  flex-basis: 50%;
}

.footer-columns {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.footer-menu-title {
  font-size: 1rem;
  text-transform: uppercase;
}

.footer-link {
  display: block;
  color: var(--white);
  text-decoration: none;
  margin: 0.5em 0;
  -webkit-transition: color 300ms;
  transition: color 300ms;
}

.footer-link:hover {
  color: #f8eda1;
}

.footer-label {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 1em;
}
.footer-input {
  margin-left: 1em;
}
.footer-bottom {
  font-size: 0.75em;
}
